<template>
    <div class="coupon-style-one" :class="stateFunc(data.isUse,data.isUseTime).background">
        <div class="top">
            <span class="price">{{ keepDisNum(data.discount) }}</span>
            <span class="type">{{ $L('折扣券') }}</span>
        </div>
        <div class="bottom">
            <div class="condtion" v-if="parseFloat(data.couponsWherePrice)"><span>{{ $L('使用条件') }}：</span>{{ data.couponsWherePrice}}</div>
            <div class="condtion" v-else-if="!parseFloat(data.couponsWherePrice)"><span>{{ $L('使用条件') }}：</span>{{ $L('全部商品可用') }}</div>
            <div class="time">
                {{ $L('有效时间') }}：
                <span>{{ data.startTime }}</span>&nbsp;-
                <span>{{ data.endTime }}</span>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "couponStyleTwo",
        props:{
            data:{
                type:Object,
                default:{}
            },
        },
        methods : {
            // 乘以10
            keepDisNum(val){
                let num = Number(val);
                return (num*10).toFixed(1);
            },
            stateFunc(isuse,time) {
                let x;
                if(isuse === 0 && time === 1){
                    x = {background:'background'}
                } else {
                    x = {background: 'backgroundgrey'};
                }
                return x;
            },

        }
    }
</script>

<style lang="scss" scoped>
    .coupon-style-one{
        height: 180px;
        color: #fff;
        background-size: 100% 100% !important;
        height: 180px;
        &.background{
            background: url("../../assets/img/distributor/coupon-bg.png");
        }
        &.backgroundgrey{
            background: url("../../assets/img/distributor/coupon-bggrey.png");
        }
        .top{
            padding: 30px 0 20px;
            .unit{
                font-size: 26px;
                display: inline-block;
                vertical-align: top;
            }
            .price{
                font-size: 50px;
                line-height: 50px;
            }
            .type{
                font-size: 16px;
                padding-left: 5px;
            }
        }
        .bottom{
            .condtion{
                padding-bottom: 20px;
            }
        }
    }
</style>