<template>
    <div class="coupon" v-loading="loading">
        <rank-tab :tab-list="theadName"></rank-tab>
        <div class="coupon-content" v-if="tableData.hasOwnProperty('list') && tableData.list.length > 0">
            <ul class="coupon-box">
                <li v-for="(item,index) in tableData.list" :key="index" class="coupon-list">
                    <div class="coupon-item" >
                        <!-- 代金券 -->
                        <coupon-style-one :data="item" v-if="item.cardtype=='CASH'"/>
                        <!-- 折扣卷 -->
                        <coupon-style-two :data="item" v-else-if="item.cardtype=='DISCOUNT'"/>
                        <!-- 旧版本的优惠卷 -->
                        <coupon-style-three :data="item" v-else-if="item.cardtype=='' && item.couponsId"/>
                        <div class="coupon-btn canUse" v-if="item.isUseTime === 1 && item.isUse === 0">
                            <el-button class="btn" type="primary" plain size="small" @click="goHome">{{ $L('立即使用') }}</el-button>
                        </div>
                        <div class="coupon-btn canUse" v-else-if="item.isUse === 1">
                            <el-button class="btn" type="primary" plain size="small" disabled>{{ $L('已使用') }}</el-button>
                        </div>
                        <div class="coupon-btn canUse" v-else-if="item.isUseTime === 2 && item.isUse === 0">
                            <el-button class="btn" type="primary" plain size="small" disabled>{{ $L('不可用') }}</el-button>
                        </div>
                        <div class="coupon-btn canUse" v-else>
                            <el-button class="btn" type="primary" plain size="small" disabled>{{ $L('已过期') }}</el-button>
                        </div>
                    </div>
                </li>
            </ul>
            <!-- 分页 -->
            <div class="list-pages">
                <el-pagination
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-size="pageSize"
                        layout="prev, pager, next, total, jumper"
                        :total="tableData.allNum">
                </el-pagination>
            </div>
        </div>
        <div class="tips" v-else>
            <p class="no-list">{{ $L('无优惠券信息') }}</p>
        </div>
    </div>
</template>

<script>
    import couponStyleOne from "../../../components/coupon/couponStyleOne.vue"
    import couponStyleTwo from "../../../components/coupon/couponStyleTwo.vue"
    import couponStyleThree from "../../../components/coupon/couponStyleThree.vue"
    import  rankTab from "../../../components/common/rankTab.vue"
    export default {
        name: "coupon",
        components : {
            rankTab,couponStyleOne,couponStyleTwo,couponStyleThree
        },
        data () {
            return {
                loading: true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                theadName:{
                    type:'canUse',
                    list:[
                        // {
                        //     text: this.$L('全部'),
                        //     type: 'all',
                        //     func: () => {
                        //         this.$router.push({params:{type:'all'}});
                        //     }
                        // },
                        {
                            text: this.$L('未使用'),
                            type: 'canUse',
                            func: () => {
                                this.$router.push({params:{type:'canUse'}});
                            }
                        },
                        {
                            text: this.$L('已使用'),
                            type: 'use',
                            func: () => {
                                this.$router.push({params:{type:'use'}});
                            }
                        },
                        {
                            text:this.$L( '已过期'),
                            type: 'over',
                            func: () => {
                                this.$router.push({params:{type:'over'}});
                            }
                        }
                    ]
                },
                tableData: {},
                pageSize: 20,
                currentPage: 1,
            }
        },
        beforeRouteUpdate (to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.currentPage = 1;
            this.getData(to);
            next();
        },
        created (){
            this.getData();
        },
        methods : {
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData(router) {
                const _self = this;
                _self.loading = true;
                if(!router){
                    router = _self.$route;
                }
                _self.theadName.type = router.params.hasOwnProperty('type') ? router.params.type : 'canUse';
                let arr = {
                    p : _self.currentPage,
                    num : _self.pageSize,
                    type : _self.theadName.type
                };
                _self.$api('getCouponList', arr , function (reData) {
                    if (reData.flag === 1) {
                        _self.tableData = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name: 'home'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name: 'home'});
                    });
                });
            },
            handleCurrentChange(val){
                this.currentPage = val;
                this.getData();
            },
            handleSizeChange(val){
                this.pageSize = val;
                this.getData();
            },
            goHome(){
                this.$gotoWebHome();
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common";
    @import "../../../assets/css/common/lib";
    .coupon{
        padding: 0 20px 20px;
        .coupon-content{
            position: relative;
            background: #fefefe;
            padding: 20px;
            margin-top: 10px;
            .coupon-box{
                display:flex;
                justify-content: flex-start;
                flex-wrap:wrap;
                box-sizing:border-box;
                width: 100%;
                text-align:left;
                .coupon-list {
                    /*min-width: 300px;*/
                    width: calc((100% - 60px)/4);
                    height: 250px;
                    &.coupon-list{
                        margin-right: 20px;
                        margin-bottom: 20px;
                        background: #f7f7f7;
                    }
                    &.coupon-list:nth-child(4n){
                        margin-right: 0;
                    }
                    .coupon-item{
                        text-align: center;
                        .coupon-btn{
                            height: 70px;
                            .btn{
                                padding: 10px 25px;
                                margin: 18px auto;
                            }
                        }
                    }
                }
            }
            .list-pages{
                text-align: center;
                padding: 40px 0 20px;
            }
        }
        .tips{
            position: relative;
            background: #fefefe;
            padding: 20px;
            margin-top: 10px;
            text-align: center;
            .no-list{
                @extend .no-select;
                position: relative;
                width: 100%;
                height: 380px;
                line-height: 380px;
                text-align: center;
                background-color: #ffffff;
                font-size: 16px;
                color: #999;
                margin-bottom: 20px;
            }
        }

        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>